
import {Alert} from "@/typings";
import {PropType, defineComponent} from "vue";

export default defineComponent({
  name: "AppAlert",
  props: {
    alert: {
      type: Object as PropType<Alert>,
      required: true
    }
  },
  emits: ['onClose'],
  methods: {
    onClickClose(alert : Alert) {
      this.$emit('onClose', alert)
    },
  },
})
